import { useState, useEffect, useRef } from "react";
import { useGlobal } from 'reactn';
import dateFormat from "dateformat"


export default function SearchManager() {

    const [ searchdate,  ] = useGlobal('searchdate');
    const [ startend,  setStartend] = useGlobal('startend');
    const [ apppolygon,  ] = useGlobal('apppolygon');


    // const { geojsonResults, loading} = useDatahub();


    const [windowSize, setWindowSize] = useState(1000 * 60 * 60 * 24)
    const [searchList, setSearchList] = useState([])
    // const [ startend, setStartend ] = useState({start:0, end:0});

    
    useEffect(() => {
        console.log('apppolygon: '+apppolygon)
        if(apppolygon == '') {
            setWindowSize(1000 * 60 * 60 * 24)
        } else {
            setWindowSize(1000 * 60 * 60 * 24 * 7)
        }
    }, [apppolygon]);


    useEffect(() => {
        console.log('searchdate: '+dateFormat(new Date(searchdate),'isoUtcDateTime').replace('Z','.000Z'))
        let SearchDate = new Date(searchdate)
        // console.log('searchdate: '+new Date(searchdate).getUTCDate())
        // console.log('day: '+ new Date(searchdate).getUTCDay())
        // console.log('month: '+new Date(searchdate).getUTCMonth())
        // console.log('year: '+new Date(searchdate).getUTCFullYear())
        console.log(SearchDate.getUTCFullYear() + '-' + SearchDate.getUTCMonth()+ '-' + SearchDate.getUTCDate())
        let _startdate = new Date(SearchDate.getUTCFullYear(), SearchDate.getUTCMonth(), SearchDate.getUTCDate())
        console.log('_startdate: '+_startdate + '/ ' + _startdate.getTime())
        let _start = dateFormat(_startdate,'isoUtcDateTime').replace('Z','.000Z')
        let _enddate = new Date(_startdate.getTime() + windowSize - 1000)
        let _end = dateFormat(_enddate,'isoUtcDateTime').replace('Z','.000Z')
        console.log('new start/end: '+_start + '/ ' + _end)

        let newSearchList = searchList

        if (newSearchList.indexOf(_start) < 0) {
            newSearchList.push(_start)
            if (newSearchList.length > 5) newSearchList.shift()
            setSearchList(newSearchList)
            setStartend({
                start: _start,
                end: _end
            })    
        } else {
            console.log('Search already done !')
        }

        
        

    }, [searchdate, windowSize]);




return [ startend ]

}
